/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { Link, graphql } from 'gatsby';
import { GatsbyImage, getSrc } from 'gatsby-plugin-image';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { Fragment } from 'react';
import type { IGatsbyImageData as GatsbyImageData } from 'gatsby-plugin-image';
import type { FC } from 'react';

import DevlogTemplateMeta from './devlog-template-meta';
import Layout from './layout';
import Seo from './seo';
import { rhythm } from './styles/typography';

type Parent = {
  excerpt: string;
};

type HeroImage = {
  sharp: {
    gatsbyImageData: GatsbyImageData;
  };
};

export type Devlog = {
  body: string;
  title: string;
  author: string;
  image: HeroImage;
  slug: string;
  date: string;
  updated: string;
  parent: Parent;
};

type DevlogTemplateProps = { data: { devlog: Devlog } };

export const query = graphql`
  query($pageID: String!) {
    devlog(id: { eq: $pageID }) {
      body
      title
      slug
      author
      date
      updated
      image {
        sharp: childImageSharp {
          gatsbyImageData(layout: CONSTRAINED, width: 700, height: 300)
        }
      }
      parent {
        ... on Mdx {
          excerpt(pruneLength: 110)
        }
      }
    }
  }
`;

const DevlogTemplate: FC<DevlogTemplateProps> = ({ data }) => {
  const {
    title,
    image,
    slug,
    body,
    date,
    updated,
    parent: { excerpt },
  } = data.devlog;

  const imgSrc = getSrc(image.sharp.gatsbyImageData);

  return (
    <Fragment>
      <nav
        css={css`
          max-width: 37em;
          margin: ${rhythm(1.5)} auto;
          padding: 0 ${rhythm(0.5)};
        `}
      >
        <ul
          css={css`
            list-style: none;
            margin: 0;
            padding: 0;
          `}
        >
          <li>
            <Link
              css={css`
                color: hsla(0, 0%, 0%, 0.63);
                font-family: 'Recursive', sans-serif;
                user-select: none;
              `}
              to="/"
            >
              &larr; back to home
            </Link>
          </li>
        </ul>
      </nav>

      <Layout>
        <Seo
          appendSiteTitleToDescription
          title={title}
          image={imgSrc}
          urlSlug={slug}
          description={excerpt}
        />

        <article
          css={css`
            > :last-child {
              margin-bottom: 0;
            }
          `}
        >
          <h1>{title}</h1>

          <DevlogTemplateMeta date={date} updated={updated} />

          <GatsbyImage
            css={css`
              box-shadow: 0 0 6px rgba(0, 0, 0, 0.11), 0 0 0 1px rgba(0, 0, 0, 0.05);
              border-radius: 3px;
              margin-bottom: ${rhythm(1)};
              pointer-events: none;
              user-select: none;
            `}
            image={image.sharp.gatsbyImageData}
            alt={title}
          />

          <MDXRenderer>{body}</MDXRenderer>
        </article>
      </Layout>

      <nav
        css={css`
          max-width: 37em;
          margin: 0 auto ${rhythm(1.5)} auto;
          padding: 0 ${rhythm(0.5)};
        `}
      >
        <ul
          css={css`
            list-style: none;
            margin: 0;
            padding: 0;
          `}
        >
          <li>
            <Link
              css={css`
                color: hsla(0, 0%, 0%, 0.63);
                font-family: 'Recursive', sans-serif;
                user-select: none;
              `}
              to="/"
            >
              &larr; back to home
            </Link>
          </li>
        </ul>
      </nav>
    </Fragment>
  );
};

export default DevlogTemplate;
